import { _decorator, Component, Sprite, resources, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('TileManager')
export class TileManager extends Component {
    @property(Sprite) // 麻将牌的Sprite组件
    private sprite: Sprite = null; 

    // 初始化麻将牌的类型、编号和序列号
    public initializeTile(type: string, number: number, index: number) {
        const spriteName = `output/combined_mj_${type}_${number}`;
        console.log(`Loading sprite at path: ${spriteName}`); // 输出路径调试信息
        resources.load(spriteName, SpriteFrame, (err, spriteFrame: SpriteFrame) => {
            if (err) {
                console.error(`Error loading sprite: ${spriteName} - ${err.message}`);
            } else {
                this.sprite.spriteFrame = spriteFrame; // 设置麻将牌的SpriteFrame
            }
        });
    }
}
